import uCharts from '@/components/u-charts/u-charts.js';
import  { isJSON } from '@/common/checker.js';
var _self;
var canvaColumn=null;
export default {
	data() {
		return {
			cWidth2:'',//横屏图表
			cHeight2:'',//横屏图表
			pixelRatio:1,
			textarea:''
		}
	},
	onLoad() {
		_self = this;











		this.cWidth2=uni.upx2px(700);
		this.cHeight2=uni.upx2px(1100);
		this.getServerData();
	},
	methods: {
		getServerData(){
			uni.request({
				url: 'https://www.ucharts.cn/data.json',
				data:{
				},
				success: function(res) {
					console.log(res.data.data)
					let ColumnColumn={categories:[],series:[]};
					//这里我后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
					ColumnColumn.categories=res.data.data.ColumnB.categories;
					ColumnColumn.series=res.data.data.ColumnB.series;
					_self.textarea = JSON.stringify(res.data.data.ColumnB);
					_self.showColumnColumn("canvasColumn",ColumnColumn);
				},
				fail: () => {
					_self.tips="网络错误，小程序端请检查合法域名";
				},
			});
		},
		showColumnColumn(canvasId,chartData){
			canvaColumn=new uCharts({
				$this:_self,
				canvasId: canvasId,
				type: 'column',
				padding:[15,15,0,15],
				legend:{
					show:true,
					padding:5,
					lineHeight:11,
					margin:0,
				},
				fontSize:11,
				background:'#FFFFFF',
				pixelRatio:_self.pixelRatio,
				animation: false,
				rotate:true,



				categories: chartData.categories,
				series: chartData.series,
				xAxis: {
					disableGrid:true,
				},
				yAxis: {
					//disabled:true
				},
				dataLabel: true,
				width: _self.cWidth2*_self.pixelRatio,
				height: _self.cHeight2*_self.pixelRatio,
				extra: {
					column: {
						type:'group',
						width: _self.cWidth*_self.pixelRatio*0.45/chartData.categories.length,
						meter:{
							//这个是外层边框的宽度
							border:4,
							//这个是内部填充颜色
							fillColor:'#E5FDC3'
						}
					}
				  }
			});
			
		},
		touchColumn(e){
			canvaColumn.showToolTip(e, {
				format: function (item, category) {
					return category + ' ' + item.name + ':' + item.data 
				}
			});
		},
		changeData(){
			if(isJSON(_self.textarea)){
				let newdata=JSON.parse(_self.textarea);
				canvaColumn.updateData({
					series: newdata.series,
					categories: newdata.categories
				});
			}else{
				uni.showToast({
					title:'数据格式错误',
					image:'../../../static/images/alert-warning.png'
				})
			}
		}
	}
}
